.top-security {
  border: 3px solid #DDDDDD;
  width: 366px;
  display: flex;
}

.security-title {
    color: #4d4d4f;
    font-family: Arial;
    font-size: 12px;
    font-weight: bold;
}

.security-description {
  color: #4d4d4f;
    font-family: Arial;
    font-size: 10px;
    margin: 10px 0px;
    a {
      color: #E60000;
    }
}

.security-content {
  padding: 5px;
}

.ssl-container {
  width: 366px;
  text-align: right;
  padding-top: 5px;
  a {
    margin-top: 10px;
    color: #000000;
    text-decoration: none;
    font: bold 7px verdana,sans-serif;
    letter-spacing: .5px;
  }
}

.banner-container {
  margin-top: 10px;
  &:hover {
    cursor: pointer;
  }
}

.security-container {
  margin-left: 35px;
}