.header-logos-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 20px;
}

.header-languages-container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  font-size: 11px;
  margin-top: 12px;
  a {
    margin: 0 12px;
    color: #4d4d4f;
    text-decoration: none;
    &:hover {
      color: red;
    }
  }
}

.red-bar {
  background-color: #E60000;
  height: 22px;
  width: 100%;
  margin-top: 12px;
}

.bar-item {
  height: 6px;
  width: 17%;
}

.color-bar-container {
  display: flex;
  margin-top: 3px;
}

.yellow {
  background-color: #FFE600;
}

.orange {
  background-color: #FF4D0A;
}

.red {
  background-color: #E60000;
}

.green {
  background-color: #037359;
}

.violet {
  background-color: #960380;
}

.blue {
  background-color: #0D4099;
}