.row {
  display: flex;
}

.row-center {
  display: flex;
  align-items: center;
}

.error-msg-title {
  color: rgb(255, 26, 55);
    font-size: 11px;
}

.error-msg {
  color: rgb(255, 26, 55);
    font-size: 12px;
    font-weight: bold;
    line-height: 22px;
}

.spinner-container {
  height: 70vh;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  text-align: center;
}