#log-container {
  width: 352px;
  height: 300px;
}

.login-title {
  font-size: 16px;
  color: #4d4d4f;
  background-image: url(../media/login_top.png);
  background-repeat: no-repeat;
    background-position: bottom left;
    height: 36px;
    width: 352px;
    display: flex;
    align-items: center;
    //padding-left: 10px;
}

.login-container {
  .login-description {
    font-size: 12px;
    margin-top: 5px;
    margin-bottom: 10px;
    text-align: center;
  }
  text-align: center;
}

.warning-message {
  vertical-align: middle;
  color: rgb(77, 77, 79);
  font-size: 11px;
  font-weight: normal;
}

.login-field {
  display: flex;
  margin: 5px 0;
  .field-name {
    width: 32%;
    font-family: Arial;
    font-size: 12px;
    text-align: right;
  }
  .field-input {
    width: 67%;
    display: flex;
    align-items: center;
    margin-left: 13px;
    input {
      width: 138px;
      border: 1px solid #ABADB3;
    color: #44697D;
    font-family: Arial, Verdana, Geneva, Helvetica, sans-serif;
    font-size: 10px;
    padding: 1px 3px 2px 3px;
    text-align: left;
    }
  }
}

.btn-wrapper {
  display: flex;
  width: 100%;
  justify-content: center;
}

.btn-general {
  font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    color: #4d4d4f;
    
    margin-top: 18px;
    &:hover {
      cursor: pointer;
    }
    
  .btn-general-left {
    height: 28px;
    width: 13px;
    font-size: 10px;
    background-image: url(../media/button_gray-left-i.png);
    background-repeat: no-repeat;
    background-position: top right;
    line-height: 22px;
    float: left;
  }
  .btn-general-center {
    height: 28px;
    width: 18px;
    font-size: 12px;
    background-image: url(../media/button_gray-center-i.png);
    background-repeat: repeat-x;
    background-position: top left;
    line-height: 22px;
    float: left;
    font-weight: bold;
    display: flex;
    align-items: center;

  }
  .btn-general-right {
    height: 28px;
    width: 13px;
    font-size: 10px;
    background-image: url(../media/button_gray-right-i.png);
    background-repeat: no-repeat;
    background-position: top right;
    line-height: 22px;
    float: left;
  }
}

.top-wrapper {
  background-image: url(../media/login_top.png);
    background-repeat: no-repeat;
    background-position: bottom left;
    width: 352px;
}

.center-wrapper {
  display: flex;
  .center-left {
    background-image: url(../media/login_left.png);
    background-repeat: repeat-y;
    background-position: top right;
    width: 4px;

  }
  .center-right {
    background-image: url(../media/login_right.png);
    background-repeat: repeat-y;
    background-position: top right;
    width: 4px;

  }
}

.bottom-wrapper {
  display: flex;
  .bottom-left {
    background-image: url(../media/login_left-bottom.png);
    background-repeat: no-repeat;
    background-position: top right;
    height: 7px;
    width: 4px;
  }
 .bottom-center {
  background-image: url(../media/login_bottom.png);
  background-repeat: no-repeat;
  background-position: top left;
  height: 7px;
  width: 352px;
 }
 .bottom-right {
  background-image: url(../media/login_right-bottom.png);
  background-repeat: no-repeat;
  background-position: top left;
  height: 7px;
  width: 4px;
}
 }

 .content-wrapper {
   margin-top: 15px;
   justify-content: center;
 }